import { NextResponse } from "next/server";

export async function GET() {
  try {
    const resp = await fetch("http://checker.soax.com/api/ipinfo", {
      // No proxy from server side â€” this returns the server's own IP
      // In production, you'd route this through your SOAX proxy
    });

    if (!resp.ok) {
      return NextResponse.json(
        { error: `Request failed with status ${resp.status}` },
        { status: resp.status }
      );
    }

    const data = await resp.json();
    return NextResponse.json(data);
  } catch (error) {
    console.error("SOAX IP check error:", error);
    return NextResponse.json(
      { error: "Failed to check IP info" },
      { status: 500 }
    );
  }
}
